// src/Page1.js

import React, { useState } from 'react';
import { motion } from 'framer-motion';
import { CSVLink } from 'react-csv';
import getEventResults from './api/api_result';

const Page1 = () => {
  const [eventId, setEventId] = useState('');
  const [tournamentName, setTournamentName] = useState('');
  const [tournamentDate, setTournamentDate] = useState('');
  const [tournamentLocation, setTournamentLocation] = useState('');
  const [results, setResults] = useState([]);
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState(null);

  const handleGetResults = async () => {
    setLoading(true);
    setError(null);
    setResults([]);

    try {
      const { tournamentName, tournamentLocation, tournamentDate, eventResults } = await getEventResults(eventId);
      setTournamentName(tournamentName);
      setTournamentLocation(tournamentLocation);
      setTournamentDate(tournamentDate);
      setResults(eventResults);
    } catch (err) {
      console.error('Error en handleGetResults:', err);
      setError('Error al obtener los resultados del evento');
    } finally {
      setLoading(false);
    }
  };

  const containerVariants = {
    hidden: { opacity: 0, x: -100 },
    visible: {
      opacity: 1,
      x: 0,
      transition: {
        type: 'spring',
        stiffness: 50,
        damping: 10,
        staggerChildren: 0.3,
      },
    },
  };

  const itemVariants = {
    hidden: { opacity: 0, y: 50 },
    visible: { opacity: 1, y: 0 },
  };

  const csvData = [
    ["Tournament Name", "Tournament ID", "Tournament Date", "Location", "Team", "Player", "Player ID", "Placement"],
    ...results.map(result => [tournamentName, eventId, tournamentDate, tournamentLocation, result.team, result.player, result.playerId, result.placement])
  ];

  return (
    <motion.div
      className="container mt-5"
      variants={containerVariants}
      initial="hidden"
      animate="visible"
    >
      <motion.h1 className="text-center mt-5" variants={itemVariants}>
        Obtener Resultados
      </motion.h1>
      <motion.div className="mb-3" variants={itemVariants}>
        <input
          type="text"
          className="form-control"
          value={eventId}
          onChange={(e) => setEventId(e.target.value)}
          placeholder="Ingrese el ID del Evento"
        />
      </motion.div>
      <motion.button
        className="btn btn-primary"
        onClick={handleGetResults}
        disabled={loading}
        variants={itemVariants}
      >
        {loading ? 'Cargando...' : 'Obtener Resultados'}
      </motion.button>
      {error && (
        <motion.div className="mt-3 text-danger" variants={itemVariants}>
          {error}
        </motion.div>
      )}
      <motion.div className="mt-3" variants={itemVariants}>
        {results.length > 0 ? (
          <ul>
            {results.map((result, index) => (
              <motion.li key={index} variants={itemVariants}>
                {`${result.team ? result.team + ' | ' : ''}${result.player} (ID: ${result.playerId}) placed ${result.placement}`}
              </motion.li>
            ))}
          </ul>
        ) : (
          !loading && <motion.p variants={itemVariants}>No hay resultados para mostrar.</motion.p>
        )}
      </motion.div>
      {results.length > 0 && (
        <motion.div className="mt-3" variants={itemVariants}>
          <CSVLink data={csvData} filename={`results-${eventId}.csv`}>
            Descargar CSV
          </CSVLink>
        </motion.div>
      )}
    </motion.div>
  );
};

export default Page1;
