// src/api/api_result.js

const backendURL = "http://localhost:8000/api/get-event-results/";
const starggKey = process.env.REACT_APP_STARTGG_API_KEY;

const getEventResults = async (eventId) => {
  if (!eventId) {
    throw new Error('Event ID is required');
  }

  try {
    const response = await fetch(backendURL, {
      method: 'POST',
      headers: {
        'Content-Type': 'application/json',
        'Accept': 'application/json',
        'Authorization': `Bearer ${starggKey}`,
      },
      body: JSON.stringify({ eventId }), // Enviamos el eventId en el cuerpo
    });

    const data = await response.json();
    console.log('Respuesta completa de la API para EventDetails:', data);

    if (response.ok && data && data.event) {
      return {
        tournamentName: data.event.tournament.name,
        tournamentLocation: data.event.tournament.venueAddress || 'Locacin no disponible',
        tournamentDate: new Date(data.event.startAt * 1000).toLocaleDateString(),
        eventResults: data.event.results || [], // Ajusta segn la estructura de tu respuesta
      };
    } else {
      console.error('Datos de respuesta no esperados para EventDetails:', data);
      throw new Error('Datos de respuesta no esperados para EventDetails');
    }
  } catch (err) {
    console.error('Error al obtener detalles del evento:', err);
    throw err;
  }
};

export default getEventResults;
